import React, { Component } from 'react';
import { Layout, Breadcrumb, Menu, Row, Col } from 'antd';
import { Footer } from 'antd/lib/layout/layout';

class SellerLayout extends Component {
  state = {
    isLoading: true,
  };

  render() {
    const { SubMenu } = Menu;
    const { Header, Content, Sider } = Layout;

    return (
        <Layout className='odr-mgr-main-layout'>
          <Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
            <div className='logo' />
            <Menu theme='dark' mode='horizontal' defaultSelectedKeys={['2']}>
              <Menu.Item key='1'>nav 1</Menu.Item>
              <Menu.Item key='2'>nav 2</Menu.Item>
              <Menu.Item key='3'>nav 3</Menu.Item>
            </Menu>
          </Header>
          <Content style={{ padding: '0 50px' }}>
            <Layout style={{ padding: '72px 0 0 0' }}>
              <Row>
                <Col md={24} xs={0}>
                  <Sider width={200} style={{ background: 'transparent' }}>
                    <Menu mode='inline' defaultSelectedKeys={['1']} defaultOpenKeys={['sub1']} className='odr-mgr layout-background'>
                      <SubMenu key='sub1' title='subnav 1'>
                        <Menu.Item key='1'>option1</Menu.Item>
                        <Menu.Item key='2'>option2</Menu.Item>
                        <Menu.Item key='3'>option3</Menu.Item>
                        <Menu.Item key='4'>option4</Menu.Item>
                      </SubMenu>
                      <SubMenu key='sub2' title='subnav 2'>
                        <Menu.Item key='5'>option5</Menu.Item>
                        <Menu.Item key='6'>option6</Menu.Item>
                      </SubMenu>
                    </Menu>
                  </Sider>
                </Col>
              </Row>
              <Layout style={{ padding: '0 0 24px 24px' }}>
                <Breadcrumb className='odr-mgr'>
                  <Breadcrumb.Item>Seller</Breadcrumb.Item>
                  <Breadcrumb.Item>Product Management</Breadcrumb.Item>
                  <Breadcrumb.Item>Add New Product</Breadcrumb.Item>
                </Breadcrumb>
                {this.props.children}
              </Layout>
            </Layout>
          </Content>
          <Footer style={{ textAlign: 'center' }}>Â© 2021 Order Manager by GanHJ</Footer>
        </Layout>
    );
  }
}

export default SellerLayout;
